<template>
    <div class="list row">
        <div>
            <subMenu></subMenu>
        </div>
        <div class="col-md-20">
            <br>
            <h4> </h4>
            <hr>
            <div>
            <form class="form-inline my-2 my-lg-0 searchbar">
                <input class="form-control mr-sm-2" type="text" v-on:keyup="searchPost" id="SearchSubject" placeholder="Search"
                       required v-model="SearchSubject" name="SearchSubject">
                <button class="btn btn-secondary my-2 my-sm-0" type="submit">Search</button>
            </form>
            </div>
            <table class="table table-hover AllPost">
                <thead>
                <tr class="table-primary">
                    <td width="10%"></td>
                    <td width="15%"></td>
                    <td></td>
                    <td width="20%"></td>
                    <td width="10%"></td>
                </tr>
                </thead>
                <tbody>
                <tr id="text-over" v-for="(post, index) in currentPosts" :key="index" >
                    <td>{{post.post_id}}</td>
                    <td>{{post.post_writer}}</td>
                    <td class="maljul"><router-link :to="{
                            name: 'post-details',
                            params: { post: post, post_id: post.post_id }
                        }">

                        <span v-on:click="updateView(post.post_id)">{{post.post_subject}}</span>
                    </router-link></td>
                    <td>{{post.created}}</td>
                    <td>{{post.post_views }}</td>
                </tr>
                </tbody>
            </table>
        </div>
        <div class="col-md-6">
            <router-view @refreshData="refreshList"></router-view>
        </div>

        <button v-if="empID==10002"class="btn btn-primary" type="button" @click="write"></button>

        <div class="pagebox">
            <ul class="pagination">
                <li class="page-item">
                    <button class="page-link" @click="gotoStart()">&laquo;</button>
                </li>
                <li class="page-item">
                    <button class="page-link" @click="prev()"><</button>
                </li>
                <li v-for="(pageNum, index) in currentPages" :key="index" class="page-item" :class="{'active':isSelected(index)}" >
                    <button class="page-link"  @click="changePage(pageNum)">{{pageNum}}</button>
                </li>
                <li class="page-item">
                    <button class="page-link" @click="next()">></button>
                </li>
                <li class="page-item">
                    <button class="page-link" @click="gotoEnd()">&raquo;</button>
                </li>
            </ul>
        </div>
    </div>

</template>

<script>
    import http from "../../http-common";
    import PostSubmenu from "./PostSubmenu";


    export default {
        name: "post-list",
        data() {
            return {
                posts: [],//   
                currentPosts: [],//[ ]    
                empID: "",
                count: 0, //  
                countList:10, //     
                totalPage:1, //    (5  )
                page:1, //   
                countPage:5,//    
                startPage:1,
                endPage:0,
                totalPages: [],// total Page for 
                currentPages: [], //     5 

                SearchSubject:""//


            };

        },
        components: {
            subMenu: PostSubmenu
        },
        methods: {
            /* eslint-disable no-console */

            //dep_pst    0()  ,  -1()
            retrieveBoards() {
                http
                    .get("/pst/posts/"+0)
                    .then(response => {
                        this.posts = response.data; // JSON are parsed automatically.
                        this.setPagination();
                        this.setCurrentPosts();
                    })
                    .catch(e => {
                        console.log(e);
                    });
            },
            //      
            setCurrentPosts() {
                this.currentPosts = [];
                let j = (this.page-1) * this.countList;
                for(let i=0; i<this.countList && j < this.posts.length; i++) {
                    this.currentPosts[i] = this.posts[j];
                    j++;
                }

            },
            setPagination() {
                //   (int) 
                this.count = this.posts.length;

                //        
                //     
                this.totalPage = this.count / this.countList; //   

                //   totalpage   
                if(this.count % this.countList > 0){
                    this.totalPage = Math.ceil(this.totalPage); //   totalPage    ()
                }
                //     
                //      .
                if(this.totalPage < this.page){
                    this.page=this.totalPage;
                }
                //totalPages   totalPage 
                //0   i +1   1 .
                for(let i=0; i<this.totalPage; i++) {
                    this.totalPages[i] = i+1;
                }
                //   
                this.startPage = ((this.page -1)/this.countPage) * this.countPage +1; //  
                //   
                if(this.totalPage < 5) {
                    this.endPage = this.totalPage;  // endPage  totalPage  
                } else {
                    this.endPage = this.startPage + this.countPage -1; //  
                }
                //    
                this.currentPages = [];
                let j = this.startPage-1;
                for(let i=0; i<=(this.endPage-this.startPage) && j <
                this.totalPage; i++) {
                    this.currentPages[i] = this.totalPages[j];
                    j++;
                }


            },
            searchPost(){
                http
                    .get("/pst/pst/SearchSubject/"+this.SearchSubject)
                    .then(response =>{
                        this.currentPosts = response.data;

                    })
            },
            refreshList() {
                this.retrieveBoards();
            },
            /* eslint-enable no-console */
            write(){
                this.$router.push({
                    path:'/pst/add'
                })
            },
            updateView(p_id){

                http
                    .put("/pst/view/"+p_id)
                    .then(response =>{
                        // eslint-disable-next-line no-console
                        this.$emit("refreshData");
                        this.$router.push('/pst/PostsList/'+p_id);
                    })
                    .catch(e =>{
                        // eslint-disable-next-line no-console
                        console.log(e);
                    });
            },

            //    
            //  setCurrentPosts .
            changePage(pageNum) {
                this.page = pageNum;
                this.setCurrentPosts();
            },
            prev() {
                if(this.startPage != 1) {
                    this.startPage = this.startPage -5;
                    this.page = this.startPage;
                    if(this.totalPage < 5) {
                        this.endPage = this.totalPage;  // endPage  totalPage  
                    } else {
                        this.endPage = this.startPage + this.countPage -1; //  
                    }

                    this.currentPages = [];
                    let j = this.startPage-1;
                    for(let i=0; i<=(this.endPage-this.startPage) && j < this.totalPage; i++) {
                        this.currentPages[i] = this.totalPages[j];
                        j++;
                    }
                    this.setCurrentPosts();
                }
            },
            next() {
                if(this.endPage < this.totalPage) {
                    this.startPage = this.endPage +1;
                    this.page = this.startPage;
                    if(this.totalPage < 5) {
                        this.endPage = this.totalPage;  // endPage  totalPage  
                    } else {
                        this.endPage = this.startPage + this.countPage -1; //  
                    }

                    this.currentPages = [];
                    let j = this.startPage-1;
                    for(let i=0; i<=(this.endPage-this.startPage) && j < this.totalPage; i++) {
                        this.currentPages[i] = this.totalPages[j];
                        j++;
                    }
                    this.setCurrentPosts();
                }
            },
            gotoStart() {
                this.changePage(1);
                this.setPagination()
            },
            gotoEnd() {
                let pack = Math.ceil(this.totalPage/this.countPage) // ()
                for(let i=0; i<pack && pack>0; i++){
                    this.next()
                    this.changePage(this.totalPage);
                }
            },
            isSelected(index) {
                /*  class   return  */
                if (index == (this.page-1)%this.countPage) {
                    return true
                } else {
                    return false
                }
            }
        },
        mounted() {
            // mounted     
            if (sessionStorage.length > 0) { //  sessionStorage    (  sessionStorage   )
                this.retrieveBoards();
                this.empID = sessionStorage.getItem("login_id"); //    empID     
                // this.paging();
            } else {
                this.$router.push("/");
            }
        },




    };

</script>

<style>
    .list {

        text-align: left;
        max-width: 800px;
        margin: auto;
    }
    .sidelist{
        position: fixed;
        left: 0;
    }
    .maljul{
        text-overflow: ellipsis; overflow: hidden;
        width: 500px;
        white-space: nowrap;

    }
    .pagebox{
        position: relative;
        margin-top: 5%;
        margin-left: 40%;
    }
    .searchbar{
        position: absolute;
        right: 13%;
        top: 15%;
    }
    .AllPost{
        width: 120%;
    }

    table{
        table-layout: fixed;
    }

</style>