<template>
    <div>
        <div>
            <h4>  </h4>
            <subMenu></subMenu>
        </div>
        <div>
        <ul>
        <div>
        <button type="button" class="btn btn-primary btn-lg" @click="getBoard">  </button>
        </div>

            <p class="smalltext" style="color: red; font-size: smaller" >*  .</p>
            <br><br>
            <div>
            <select class="custom-select" v-model="selectBoard">
                <option disabled value="">  </option>
                <option v-for="(board,index) in boards" :key="index">
                    {{board.board_name}}
                </option>
            </select>
            </div>
            <br>
            <span>{{selectBoard}}</span>
                <br>
             <br>
            <div>
            <button class="btn btn-success" type="button" @click="deleteBoard"></button>
            <button class="btn btn-success" type="button" @click="backlist"></button>
            </div>
        </ul>
        </div>

    </div>
</template>

<script>
    import http from "../../http-common";
    import PostSubmenu from "./PostSubmenu";

    export default {
        name: "Delete-Board",
        data () {
            return{
                boards:[], //   .
                dep_id: "", //    
                selectBoard:"",
                board_name:""
            };
        },
        components: {
            subMenu: PostSubmenu
        },
        methods:{
            /* eslint-disable no-console */
            //    
            deleteBoard(){
                    let result= confirm("     .\n  ?")
                    if(result){
                     http
                        .delete("/board/deleteBoard/" + this.selectBoard)
                        alert(" .")
                        history.go(0);
                    }else{
                        // history.go(0);
                    }
                    console.log(this.selectBoard+"<<<<<<< ")

            },
            getBoard(){
                http
                    .get("/board/getBoard/" + this.dep_id)
                    .then(response =>{
                        this.boards=response.data;
                        alert(" !\n   !")
                    })

            },


            backlist(){
                history.go(-1);
            },

            getEmpInfo(id) {    //  id this.employee.emp_id . : mounted()  .
                http
                    .post("/mypage/" + id)
                    .then(response => {
                        //   employee  .
                        this.dep_id = response.data.dep_id;
                    })
                    .catch(e => {
                        /* eslint-disable no-console */
                        console.log(e);
                    });
            },
            writeCheck() {
                let write_chk = location.href.indexOf("dep_pst");  // dep_pst    0()  ,  -1()
                console.log(write_chk);
                if(write_chk==-1) {
                    //     
                    this.post_dep_id = 0;   //  post_dep_id  0
                } else {
                    this.getEmpInfo(this.empID);    // empID   : dep_id 

                }
            }
        },
        mounted() {
            if (sessionStorage.length > 0) { //  sessionStorage    (  sessionStorage   )
                this.empID = sessionStorage.getItem("login_id"); //    empID     
                // this.getName(this.empID);   // empID    
                this.getEmpInfo(this.empID);

            } else {
                this.$router.push("/");
            }
        },
        updated() {
            this.writeCheck();
        }

    }
</script>

<style scoped>
    .custom-select{
        width: 30%;
    }

</style>