<template>                                                                  <!-- -->
    <div class="list row">
        <AddressSubMenu></AddressSubMenu>
        <div >
            <h4> </h4>
            <hr>
            <div class="form-inline my-2 my-lg-0" style="margin-bottom: 2px; float: right">
                <!--, -->
                <fieldset>
                    <input type="text" class="form-control mr-sm-2" v-on:keypress="searchOutAddress"
                           placeholder=",  " id="nameAndCompany"
                           required v-model="nameAndCompany" name="nameAndCompany"/>

                    <button class="btn btn-secondary my-2 my-sm-0" v-on:click="searchOutAddress"></button>
                    <button class="btn btn-secondary my-2 my-sm-0" v-on:click="refreshList"></button>
                </fieldset>
            </div>


            <table boder="2" class="table table-hover" style="margin-top: 70px">
                <!---->
                <thead class="table-primary">
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <td> </td>

                </thead>
                <tr v-for="(outAddress,index) in currentPosts" :key="index">
                    <td>

                        {{outAddress.outName}}

                    </td>
                    <td>{{outAddress.out_mobile}}</td>
                    <td>{{outAddress.out_email}}</td>
                    <td>{{outAddress.outCompany}}</td>
                    <td>{{outAddress.out_dep_phone}}</td>
                </tr>
            </table>

            <div>
                <ul class="pagination">
                    <li class="page-item">
                        <button class="page-link" @click="gotoStart()">&laquo;</button>
                    </li>
                    <li class="page-item">
                        <button class="page-link" @click="prev()"><</button>
                    </li>
                    <li v-for="(pageNum, index) in currentPages" :key="index" class="page-item"
                        :class="{'active':isSelected(index)}">
                        <button class="page-link" @click="changePage(pageNum)">{{pageNum}}</button>
                    </li>
                    <li class="page-item">
                        <button class="page-link" @click="next()">></button>
                    </li>
                    <li class="page-item">
                        <button class="page-link" @click="gotoEnd()">&raquo;</button>
                    </li>
                </ul>
            </div>
        </div>

    </div>
</template>


<script>
    import http from "../../../http-common";
    import AddressSubMenu from "../AddressSubMenu";

    export default {
        name: "outAddressMain",
        data() {
            return {
                nameAndCompany: "",
                outaddress: [],
                currentPosts: [],
                count: 0,   // 
                countList: 5, //     
                totalPage: 1, //    (5  )
                page: 1,
                countPage: 5,
                startPage: 1,
                endPage: 0,
                totalPages: [],
                currentPages: [], //     5 

            }
        },
        methods: {
            setPagination() {
                this.count = this.outaddress.length;
                console.log(this.outaddress.length + " ")
                this.totalPage = this.count / this.countList; //   
                if (this.count % this.countList > 0) {
                    this.totalPage = Math.ceil(this.totalPage);
                }
                if (this.totalPage < this.page) {
                    this.page = this.totalPage;
                }

                for (let i = 0; i < this.totalPage; i++) {
                    this.totalPages[i] = i + 1;
                }

                this.startPage = ((this.page - 1) / this.countPage) * this.countPage + 1; //  

                if (this.totalPage < 5) {
                    this.endPage = this.totalPage;  // endPage  totalPage  
                } else {
                    this.endPage = this.startPage + this.countPage - 1; //  
                }

                this.currentPages = [];
                let j = this.startPage - 1;
                for (let i = 0; i <= (this.endPage - this.startPage) && j <
                this.totalPage; i++) {
                    this.currentPages[i] = this.totalPages[j];
                    j++;
                    console.log("curr " + i + " " + this.currentPages[i]);
                    console.log("startPage : " + this.startPage)
                    console.log("endPage : " + this.endPage)
                    console.log("totalPage : " + this.totalPage)
                    console.log("totalPages : " + this.totalPages)

                }


            },
            retrieveOutAddress() {
                http
                    .get("/outaddress/outaddress")
                    .then(response => {
                        this.outaddress = response.data; // JSON are parsed automatically.
                        console.log(response.data);
                        this.setPagination();
                        this.setCurrentPosts();

                    })
                    .catch(e => {
                        console.log(e);
                    });
            },
            setCurrentPosts() {
                this.currentPosts = [];
                let j = (this.page - 1) * this.countList;
                for (let i = 0; i < this.countList && j < this.outaddress.length; i++) {
                    this.currentPosts[i] = this.outaddress[j];
                    j++;
                }

            },

            refreshList() {
                this.retrieveOutAddress();
                this.nameAndCompany = "";
            },

            searchOutAddress() {                                                         /*,  */
                if (this.nameAndCompany == "") return;
                http
                    .get("/outaddress/outaddress/nameAndCompany/" + this.nameAndCompany)
                    .then(response => {
                        this.currentPosts = response.data, // JSON are parsed automatically.
                            console.log(response.data);
                    })
                    .catch(e => {
                        console.log(e);
                    });
            },
            changePage(pageNum) {
                this.page = pageNum;
                this.setCurrentPosts();
                console.log(this.page)
            },
            isSelected(index) { //<< >> ,   
                /*  class   return  */
                if (index == (this.page - 1) % this.countPage) {
                    return true
                } else {
                    return false
                }
            },
            prev() {
                if (this.startPage != 1) {
                    this.startPage = this.startPage - 5;
                    this.page = this.startPage;
                    if (this.totalPage < 5) {
                        this.endPage = this.totalPage;  // endPage  totalPage  
                    } else {
                        this.endPage = this.startPage + this.countPage - 1; //  
                    }

                    this.currentPages = [];
                    let j = this.startPage - 1;
                    for (let i = 0; i <= (this.endPage - this.startPage) && j < this.totalPage; i++) {
                        this.currentPages[i] = this.totalPages[j];
                        j++;
                        console.log("curr " + i + " " + this.currentPages[i]);
                    }
                    this.setCurrentPosts();
                }
            },
            next() {
                if (this.endPage < this.totalPage) {
                    this.startPage = this.endPage + 1;
                    this.page = this.startPage;
                    if (this.totalPage < 5) {
                        this.endPage = this.totalPage;  // endPage  totalPage  
                    } else {
                        this.endPage = this.startPage + this.countPage - 1; //  
                    }

                    this.currentPages = [];
                    let j = this.startPage - 1;
                    for (let i = 0; i <= (this.endPage - this.startPage) && j < this.totalPage; i++) {
                        this.currentPages[i] = this.totalPages[j];
                        j++;
                        console.log("curr " + i + " " + this.currentPages[i]);
                    }
                    this.setCurrentPosts();
                }
            },
            gotoStart() {
                // location.reload();
                this.changePage(1);
                this.setPagination()
            },
            gotoEnd() {
                let pack = Math.ceil(this.totalPage / this.countPage)//()
                for (let i = 0; i < pack; i++)
                    this.next()
                this.changePage(this.totalPage);
            }

        },
        mounted() {
            // mounted     
            if (sessionStorage.length > 0) { //  sessionStorage    (  sessionStorage   )
                this.retrieveOutAddress();
            } else {
                this.$router.push("/");
            }

        },
        components: {
            AddressSubMenu

        },

    }
</script>

<style scoped>
    .list {
        text-align: center;
        max-width: 90%;
        /*  margin: auto;*/
        margin-left: 15%;
    }


    .table {
        width: 80%;
        margin: auto;
        text-align: center;
    }

</style>