<template>
    <div class="newBoard"><h4 style="font-weight: bold">  </h4><br>
        <div>
            <div>
                <subMenu></subMenu>
            </div>

            <div v-if="!submitted">
                <!--          -->
                <div class="newcontent">
                    <div class="form-group " >
                        <label style="font-weight: bold; margin-left: 30%;"></label>
                        <input type="text" class="form-control " style="width: 150%;" id="writer" required v-model="post.post_writer" name="writer" placeholder="" readonly>


                    </div>  <br>
                    <div class="form-group ">
                        <label style="font-weight: bold; margin-left: 30%;"> </label>
                        <input type="text" class="form-control" style="width: 150%;" id="subject" required v-model="board_name" name="subject" placeholder=" ">
                    </div>

                    <br>
                </div>
                <div class="boardbutton">
                <button v-on:click="saveBoard" class="btn btn-success"></button>
                <button class="btn btn-danger" @click="backlist"></button>
                </div>
            </div>

            <div v-else>
                <h4>   !!</h4>
                <button class="btn btn-success" @click="backlist"></button>
            </div>
        </div>
    </div>

</template>

<script>
    import PostSubmenu from "./PostSubmenu";
    import http from "../../http-common";
    import router from "../../router";

    export default {
        name: "board",
        data() {
            return {
                post: {
                    post_id: "",
                    board_name: "",
                    post_writer: "",
                    post_subject: "",
                    post_content: "",
                    post_created: "",
                    post_view: ""
                },
                submitted: false,
                empID: "",
                dep_id: "",
                board_name:""



            };
        },
        components: {
            subMenu: PostSubmenu
        },
        methods: {

            /* eslint-disable no-console */
            //   
            testbutton() {
                console.log(this.dep_id)
            },
            //    
            saveBoard(){
                var Board ={
                    board_name:this.board_name,
                    dep_id:this.dep_id
                };
                http
                    .post("/board/saveBoard",Board)
                    .then(response =>{
                    })
                alert("  !!");
                this.$router.push("/dep_pst");
            },
            //   
            backlist(){
                history.go(-1);
            },
            getName(login_id) {
                /*      */
                http
                    .post("/login/name/" + login_id)
                    .then(response => {
                        this.post.post_writer = response.data; // survey_writer     .
                    })
            }, // End - getName()
            getEmpInfo(id) {    //  id this.employee.emp_id . : mounted()  .
                http
                    .post("/mypage/" + id)
                    .then(response => {
                        //   employee  .
                        this.dep_id = response.data.dep_id;
                        console.log(this.dep_id+"   depID?")
                    })
                    .catch(e => {
                        /* eslint-disable no-console */
                        console.log(e);
                    });
            },
            writeCheck() {
                let write_chk = location.href.indexOf("dep_pst");  // dep_pst    0()  ,  -1()
                console.log(write_chk);
                if(write_chk==-1) {
                    //     
                    this.post_dep_id = 0;   //  post_dep_id  0
                } else {
                    this.getEmpInfo(this.empID);    // empID   : dep_id 
                }
            }
        },
        mounted() {
            // mounted     
            if (sessionStorage.length > 0) { //  sessionStorage    (  sessionStorage   )
                this.empID = sessionStorage.getItem("login_id"); //    empID     
                this.getName(this.empID);   // empID    
            } else {
                this.$router.push("/");
            }
        },
        updated() {
            this.writeCheck();
        }
    };
</script>

<style scoped>

    .newBoard_cont{
        border: 1px solid #dddddd;
        border-radius: 10px;
        width: 40%;
        margin: 10px auto;
    }
    .newBoard_cont_form {
        width: 100%;

    }


    .btn btn-success{
        display: none;
    }
    .newBoard{

    }
    .newcontent{
        position: absolute;
        left: 42%;
    }
    .boardbutton{
        position: absolute;
        left: 45%;
        top: 50%;
    }
</style>