<template>
    <div>
        <h3 style="margin: 20px; font-weight: bold"> </h3>
    <div class="root">
    <div>
        <div>
            <subMenu></subMenu>
        </div>

        <div v-if="!submitted"  class="ppo">
<!--          -->
            <div class="selectbox" style="margin-bottom:10px;">
            <label class="labelfont">  &nbsp;&nbsp;&nbsp;&nbsp; </label>
            <select class="custom-select" v-model="post.board_name">
                <option disabled value=""> </option>
                <option v-for="(board, index) in boards" :key="index">{{board.board_name}}</option>
            </select>
            </div>
            <hr>
            <br>
            <div class="form-group form-inline form-group-JH" >
                <label for="writer" class="labelfont">&nbsp;&nbsp;&nbsp;</label>
                <input type="email" class="form-control col-sm-9" id="writer"required v-model="post.post_writer" name="writer" readonly>
            </div>
            <div class="form-group form-inline form-group-JH">
                <label for="writer" class="labelfont"> &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</label>
                <input type="email" class="form-control col-sm-9" id="subject"required v-model="post.post_subject" name="subject" aria-describedby="emailHelp" placeholder=" ">
            </div>
            <div class="form-group form-inline form-group-JH">
                <label for="writer" class="labelfont">  &nbsp;&nbsp;&nbsp;</label>
                <textarea  class="form-control col-sm-9" id="exampleTextarea" rows="10" required v-model="post.post_content" name="content" placeholder="1000  "></textarea>
            </div>
            <br>
            <div class="form-group form-inline form-group-JH">
                <label for="exampleInputFile" class="labelfont"> </label>
                <input class="form-control-file" id="exampleInputFile" aria-describedby="fileHelp" type="file">
                <small class="form-text text-muted" id="fileHelp"> .jpg .png  </small>
            </div>

        <button v-on:click="savePost" class="btn btn-success"> </button> &nbsp;&nbsp;&nbsp;&nbsp;
            <button v-on:click="cnlth" class="btn btn-success"></button>
    </div>

    </div>
    </div>
    </div>
</template>

<script>
    import http from "../../http-common";
    import PostSubmenu from "./PostSubmenu";
    export default {
        name: "add-post",
        data() {
            return {
                post: {
                    post_id: "",
                    board_name: "",
                    post_writer: "",
                    post_subject: "",
                    post_content: "",
                    post_created: "",
                    post_view: ""
                },
                submitted: false,
                empID: "",
                post_dep_id: "",
                boards:[]
            };
        },
        components: {
            subMenu: PostSubmenu
        },
        methods: {

            /* eslint-disable no-console */
            //   
            savePost() {
                var data = {
                    post_id: this.post.post_id,
                    board_name: this.post.board_name,
                    post_dep_id: this.post_dep_id,
                    post_writer: this.post.post_writer,
                    post_subject: this.post.post_subject,
                    post_content: this.post.post_content,
                    post_created: this.post.post_created,
                    post_view: this.post.post_view,
                    emp_id:this.emp_id
                };
                    if(data.post_subject==null || data.post_subject==""){
                        alert("  ");
                    }else if(data.post_content==null || data.post_content=="") {
                        alert(" ");
                    }else if(data.board_name==null || data.board_name==""){
                        alert("  !");
                    }else{
                        //    
                        http
                            .post("/pst/post", data)
                            .then(response => {
                                this.post.post_id = response.data.post_id;
                                console.log(response.data);
                            })
                            .catch(e => {
                                console.log(e);
                            });
                        this.submitted = true;
                        alert("   !");
                        this.$router.push({
                            path: '/dep_pst'
                        });
                    }
            },
            // 
            backlist(){
                history.go(-1);
            },
            getName(login_id) {
                /*      */
                http
                    .post("/login/name/" + login_id)
                    .then(response => {
                        this.post.post_writer = response.data; // survey_writer     .
                    })
            }, // End - getName()
            getEmpInfo(id) {    //  id this.employee.emp_id . : mounted()  .
                http
                    .post("/mypage/" + id)
                    .then(response => {
                        //   employee  .
                        this.post_dep_id = response.data.dep_id;
                        this.getBoard();
                    })
                    .catch(e => {
                        /* eslint-disable no-console */
                        console.log(e);
                    });
            },
            writeCheck() {
                let write_chk = location.href.indexOf("dep_pst");  // dep_pst    0()  ,  -1()
                console.log(write_chk);
                if(write_chk==-1) {
                    //     
                    this.post_dep_id = 0;   //  post_dep_id  0
                } else {
                    this.getEmpInfo(this.empID);    // empID   : dep_id 
                }
            },
            //  board_name   
            getBoard(){
                http
                    .get("/board/getBoard/" + this.post_dep_id)
                    .then(response =>{
                        this.boards=response.data;
                    })

            },
            //    
            cnlth(){
                this.$router.push({
                    path:'/dep_pst/'
                })
            },
        },
        mounted() {
            // mounted     
            if (sessionStorage.length > 0) { //  sessionStorage    (  sessionStorage   )
                this.empID = sessionStorage.getItem("login_id"); //    empID     
                this.getName(this.empID);   // empID    
                this.writeCheck();
            } else {
                this.$router.push("/");
            }
        },
        // updated() {
        //     this.writeCheck();
        // }
    };
</script>

<style scoped>
    .ppo{
        text-align: center;
        max-width: 650px;
        width: 100%;
        margin: 0 auto;

    }
    .form-group-JH{
        width: 700px !important;
    }
    .root{
        width: 800px;
        border: silver solid 1px;
        margin-left: 25%;
        padding: 20px 0 50px 0;
        position: static;
        left: 24%;

    }
    .btn btn-success{
        display: none;
    }
    .selectbox{
        padding: 10px 0 0px 0;

    }
    .labelfont{
        font-weight: bold;
    }
</style>