<template>
    <div>
        <div v-if="!loginInfo.manager" class="topMenu">
            <div>
                <nav class="navbar navbar-expand-lg navbar-dark bg-primary">
                    <router-link class="navbar-brand" to="/">upffice</router-link>
                    <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarColor01"
                            aria-controls="navbarColor01" aria-expanded="false" aria-label="Toggle navigation">
                        <span class="navbar-toggler-icon"></span>
                    </button>

                    <div class="collapse navbar-collapse" id="navbarColor01">
                        <ul class="navbar-nav mr-auto">
                            <li class="nav-item">
                                <router-link class="nav-link" to="/calendar"></router-link>
                            </li>
                            <li class="nav-item">
                                <router-link class="nav-link" to="/working"></router-link>
                            </li>
                            <li class="nav-item">
                                <router-link class="nav-link" to="/app"></router-link>
                            </li>
                            <li class="nav-item">
                                <router-link class="nav-link" to="/pst"></router-link>
                            </li>
                            <li class="nav-item">
                                <router-link class="nav-link" to="/survey"></router-link>
                            </li>
<!--                            <li class="nav-item">-->
<!--                                <router-link class="nav-link" to="/file"></router-link>-->
<!--                            </li>-->
                            <li class="nav-item">
                                <router-link class="nav-link" to="/mainEmployAddress"></router-link>
                            </li>
                        </ul>

                        <!--     => v-if : loginInfo.login_status      -->
                        <form v-if="!loginInfo.login_status" class="form-inline my-2 my-lg-0">
                            <span style="color: white"> </span>
                        </form>

                        <!--    => v-else :    top menu   ,      -->
                        <form v-else class="form-inline my-2 my-lg-0">
                            <div class="img_div">
                                <img v-bind:src="emp_img_url"><br>
                            </div>
                            <router-link :to="{ name: 'mypage'}"> <!-- name mypage  router  -->
                                <span style="color: white">{{login_name}}({{loginInfo.login_id}}) </span>
                                <!--  : mounted()  ,    getName()  -->
                            </router-link>&nbsp;&nbsp;&nbsp;
                            <button class="btn btn-secondary my-2 my-sm-0" v-on:click="logout">Logout</button>
                            <!--   -->
                        </form>

                    </div>
                </nav>
            </div>
            <br/>
        </div>
        <div v-else-if="loginInfo.manager" class="topMenu">
            <div>
                <nav class="navbar navbar-expand-lg navbar-dark bg-primary">
                    <router-link class="navbar-brand" to="/">upffice</router-link>
                    <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarColor01"
                            aria-controls="navbarColor01" aria-expanded="false" aria-label="Toggle navigation">
                        <span class="navbar-toggler-icon"></span>
                    </button>
                    <div class="collapse navbar-collapse" >
                        <ul class="navbar-nav mr-auto">
                            <li class="nav-item">
                                <router-link class="nav-link" to="/manager/employees"></router-link>
                            </li>
                            <li class="nav-item">
                                <router-link class="nav-link" to="/manager/out-address"></router-link>
                            </li>
                            <li class="nav-item">
                                <router-link class="nav-link" to="/manager/form-management"></router-link>
                            </li>
                        </ul>

                        <span style="color: white"> </span>&nbsp;&nbsp;&nbsp;
                        <form v-if="loginInfo.manager" class="form-inline my-2 my-lg-0">
                            <button class="btn btn-secondary my-2 my-sm-0" v-on:click="logout">Logout</button>
                            <!--   -->
                        </form>
                    </div>
                </nav>
            </div>
        </div>
    </div>
</template>

<script>
    import http from "../http-common";

    export default {
        data() {
            return {
                customers: [],
                loginInfo: {
                    login_status: "",
                    login_id: "",
                    manager: ""
                },
                cnt: 0,
                login_name: "",
                emp_img_url: "" //   
            };
        },
        methods: {
            logout() {
                /*   */
                sessionStorage.clear();         // logout     : sessionStorage.length  0 .
                this.$router.push("/"); // logout     
            }, // End - clearInput()
            getName() {
                /*      */
                http
                    .post("/login/name/" + this.loginInfo.login_id)
                    .then(response => {
                        this.login_name = response.data;
                    })
            }, // End - getName()
            managerOrNot(){
                if(this.loginInfo.login_id==='manager'){
                    this.manager=true;
                }
            }
        },
        mounted() {
            /*    :       length  0 . */
            if (sessionStorage.length > 0) {
                // TopMenu       
                this.loginInfo.login_id = sessionStorage.getItem("login_id");
                this.loginInfo.login_status = sessionStorage.getItem("login_status");
                this.loginInfo.manager = sessionStorage.getItem("isManager");

                //       . :  ,   
                //    
                //   .jpg      
                if(!this.loginInfo.manager) {
                    this.getName(); //    
                    this.emp_img_url = 'http://localhost:8080/emp_img/'+ this.loginInfo.login_id + '.jpg';  //    
                }

            }
        }

    };
</script>

<style scoped>
    .topMenu {
        text-align: center;
    }

    .btn-secondary {
        margin-right: 5px;
    }

    .img_div {
        width: 30px;
        height: 30px;
        border-radius: 70%;
        overflow: hidden;
        margin-right: 10px;
    }

    img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
</style>
