<template>
    <div class="mainForm">
        <!--    <div>  -->
        <div v-if="!loginInfo.loginStatus && !isManager">
            <div class="form-group">
                <label class="col-form-label col-form-label-lg inputLbl" for="emp_id"></label>
                <input class="form-control form-control-lg" type="text" id="emp_id" v-on:keyup.enter="login" required
                       v-model="input.emp_id" placeholder=" ">
            </div>

            <div class="form-group">
                <label class="col-form-label col-form-label-lg inputLbl" for="emp_pw"></label>
                <input class="form-control form-control-lg" type="password" v-on:keyup.enter="login" id="emp_pw"
                       required v-model="input.emp_pw" placeholder=" ">
            </div>
            <br>

            <button v-on:click="login()" class="btn btn-primary btn-lg loginBtn">Login</button>
        </div>

        <div v-else-if="!isManager">
            <myInfo></myInfo>
            <working></working>
            <sign></sign>
            <calendar></calendar>
            <today></today>
            <!--ex) {{this.$router.push("/mypage")}}-->
            <!--       push   .(  )-->
        </div>

        <div v-else-if="isManager">
            <h4>You logIned successfully!</h4>
                
            ex) {{this.$router.push("/manager/employees")}}
            <!--       push   .(  )-->
        </div>
    </div>
</template>

<script>
    import http from "../http-common";
    import working from "./main/Working";
    import calendar from "./main/MainCalendar";
    import myInfo from "./main/MyInfo";
    import sign from "./main/SignSummary";
    import today from "./main/TodaySchedule";

    export default {
        name: "employee-login",
        data() {
            return {
                input: {
                    emp_id: "",    //      
                    emp_pw: "",     //      
                    manager_id: "",
                    manager_pw: ""
                },
                loginInfo: {
                    loginStatus: "", //   
                    loginId: ""      //  
                },
                isManager : ""   //    
            };
        },
        components: {
            myInfo:myInfo,
            working:working,
            sign:sign,
            calendar:calendar,
            today:today
        },
        methods: {
            login() {
                if (this.input.emp_id != "" && this.input.emp_pw != "") { // ,  input   
                    var data = {
                        emp_id: this.input.emp_id,
                        emp_pw: this.input.emp_pw,

                    };

                    if((this.input.emp_id === "10000") && (this.input.emp_pw==="0000")){
                        this.isManager = true;
                        sessionStorage.setItem("isManager", this.isManager);
                        sessionStorage.setItem("login_id", this.loginInfo.loginId);           // sessionStorage   () 
                        for (let i = 0; i < 1; i++) {
                            location.reload();//     
                        }

                        // sessionStorage.setItem("login_status", this.loginInfo.loginStatus);   // sessionStorage    
                    } else{

                    // UpfficeBack  LoginController  
                    http
                        .post("/login", data)
                        .then(response => {

                            if (response.data != 0) {  //   0  DB (),   
                                this.loginInfo.loginId = response.data;   // loginId   .(  )
                                this.loginInfo.loginStatus = true;        // loginStatus     true 

                                sessionStorage.setItem("login_id", this.loginInfo.loginId);           // sessionStorage   () 
                                sessionStorage.setItem("login_status", this.loginInfo.loginStatus);   // sessionStorage    


                                for (let i = 0; i < 1; i++) {
                                    location.reload();//     
                                }

                            } else {
                                alert("     !");
                            }

                        })
                        .catch(e => {
                            /* eslint-disable no-console */
                            console.log(e);
                        });
                    }

                } else { // ,  input   
                    alert("  !");
                } // End : if-else
            } // End - login()

        }, // End - methods
        mounted() {
            if (sessionStorage.length > 0) {
                //   <div>    this.loginInfo.loginStatus  .
                this.loginInfo.loginStatus = sessionStorage.getItem("login_status");
                this.isManager = sessionStorage.getItem("isManager");
            }
        }
    };
</script>

<style>
    .mainForm {
        max-width: 300px;
        margin: 100px auto auto;
    }

    .inputLbl {
        margin-right: 220px;
        font-weight: bold;
    }

    .loginBtn {
        width: 300px;
    }
</style>
