<template>
    <div class="container">
        <div class="modal-dialog" role="document">
            <div class="modal-content">
                <div class="modal-header">
                    <h5 class="modal-title">  </h5>
                </div>
                <!--  /  -->
                <div class="modal-body searchResult">
                    <table class="table table-hover searchResultTable">
                        <thead>
                            <tr class="table-active">
                                <th> </th>
                                <th> </th>
                                <th> </th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr v-for="(schedule, index) in searchResult" :key="index" @click="showDetail(schedule)">
                                <td>{{schedule.sche_name}}</td>
                                <td>{{schedule.sche_start_date}}</td>
                                <td>{{schedule.sche_end_date}}</td>
                            </tr>
                        </tbody>

                    </table>
                </div>

                <div class="modal-footer">
                    <button type="button" class="btn btn-secondary" data-dismiss="modal" @click="cancel"></button>
                </div>
            </div>
            <modals-container/>
        </div>
    </div>
</template>

<script>
    import http from "../../http-common";
    import ScheduleDetailModal from "./ScheduleDetailModal";

    export default {
        props: ['searchResult'],
        data() {
            return{
            }
        },
        methods : {
            showDetail(schedule) { //  ,   modal  
                this.$modal.show(ScheduleDetailModal, {
                        name: 'ScheduleDetailModal',
                        schedule: schedule,
                        modal: this.$modal,
                    },
                    {
                        width: '400px',
                        height: '600px',
                        draggable: true,
                    });
            },
            cancel() {
                this.$emit('close');
                for(let i=0; i<1; i++) location.reload();
            }
        }
    }
</script>

<style scoped>
    .searchResult {
        height: 215px;
        overflow: scroll;
        text-overflow: ellipsis; /*   */
        white-space: nowrap; /*   */
        overflow-x: hidden; /*  */
        -ms-overflow-style: none; /*IE   */
    }
    .searchResult::-webkit-scrollbar { /*IE     */
        width: 1px;
        background: transparent;
    }
</style>