<template>
        <div class="modal-dialog modalMagin" role="document">
            <div class="modal-content">
                <div class="modal-header">
                    <h5 class="modal-title"> </h5>
                    <button type="button" class="close" v-on:click="$emit('close')" aria-label="Close">
                        <span aria-hidden="true">X</span>
                    </button>
                </div>
                <div class="modal-body">
                       .
                    <input type="date" :min="min" :max="max" v-model="applyDate" class="form-control">
                </div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-primary" @click="applyAnnual()"></button>
                    <button type="button" class="btn btn-secondary" @click="$emit('close')"></button>
                </div>
            </div>
        </div>
</template>
<script>
    import http from "../../http-common";

    export default {
        name: "working-modal",
        data: function () {
            return {
                applyDate: '',
                min: '',
                max: '',
                empId: ''
            }
        }, props: [
            'hot_table',
        ], methods: {
            /* eslint-disable no-console */
            applyAnnual() {//     
                http
                    .get("/working/applyAnnual/" + this.empId + "?date=" + this.applyDate)
                    .then(response => {
                        alert(this.applyDate + "   ");
                        location.reload();
                    })
                    .catch(e => {
                        console.log(e);
                    });
            },// End - applyAnuual()
            settingInput() {// Input   
                var moment = require('moment');
                this.min = moment().format('YYYY-MM-DD');
                this.max = moment().endOf('year').format('YYYY-MM-DD');
            }// End - settingInput()
            /* eslint-enable no-console */
        },
        mounted() {
            if (sessionStorage.length > 0) { //  sessionStorage    (  sessionStorage   )
                this.empId = sessionStorage.getItem("login_id");// sessionStorage  
                this.settingInput();
            }
        }// End - mounted()
    };// End - export default
</script>
<style>
    .modalMagin{
        margin: 1px;
    }
</style>