
<template>

    <div class="list row">

        <div>
            <subMenu></subMenu>
        </div>
        <div class="col-md-20">
            <br>
            <h4>{{this.board_name}} </h4>
            <hr>
            <div>
                <form class="form-inline my-2 my-lg-0 searchbar">
                    <input class="form-control mr-sm-2" type="text" placeholder="Search">
                    <button class="btn btn-secondary my-2 my-sm-0" type="submit">Search</button>
                </form>
            </div>
            <table class="table table-hover AllPost">
                <thead>
                <tr class="table-primary">
                    <td width="10%"></td>
                    <td width="15%"></td>
                    <td></td>
                    <td width="20%"></td>
                    <td width="10%"></td>
                </tr>
                </thead>
                <tbody>
                <tr id="text-over" v-for="(post, index) in currentPosts" :key="index">
                    <td>{{post.post_id}}</td>
                    <td>{{post.post_writer}}</td>
                    <td class="maljul"><router-link :to="{
                            name: 'dep_post-details',
                            params: { post: post, post_id: post.post_id }
                        }">

                        <span >{{post.post_subject}}</span>
                    </router-link></td>
                    <td>{{post.created}}</td>
                    <td>{{post.post_views }}</td>

                </tr>

                </tbody>
            </table>
            <button class="btn btn-primary" type="button" style="position: absolute; left: 80%;" @click="write"></button>
            <div class="pagebox">
                <ul class="pagination">
                    <li class="page-item">
                        <button class="page-link" @click="gotoStart()">&laquo;</button>
                    </li>
                    <li class="page-item">
                        <button class="page-link" @click="prev()"><</button>
                    </li>
                    <li v-for="(pageNum, index) in currentPages" :key="index" class="page-item" :class="{'active':isSelected(index)}" >
                        <button class="page-link"  @click="changePage(pageNum)">{{pageNum}}</button>
                    </li>
                    <li class="page-item">
                        <button class="page-link" @click="next()">></button>
                    </li>
                    <li class="page-item">
                        <button class="page-link" @click="gotoEnd()">&raquo;</button>
                    </li>
                </ul>
            </div>
        </div>

    </div>
</template>
<script>
    import http from "../../http-common";
    import PostSubmenu from "./PostSubmenu";

    export default {
        name: "DepPage",
        data(){
            return{
                dep_board :[],
                board_name : "",
                dep_id : "",
                currentPosts: [],
                count: 0,
                countList:5,
                totalPage:1,
                page:1,
                countPage:5,
                startPage:1,
                endPage:0,
                totalPages: [],
                currentPages: [],
            };

        },
        components: {
            subMenu: PostSubmenu
        },

        methods: {

            //PostSubmenu   param     
            // parameter       .
            write(){
                this.$router.push({
                    path:'/dep_pst/add'
                })
            },
            setBoard(){
                this.board = this.$route.params;
                this.board_name = this.$route.params.board_name;
                this.BoardAll();

            },

            //    name      
            BoardAll(){
                http
                .get("/pst/dep_posts/"+this.board_name)
                .then(response =>{
                    this.dep_board=response.data;
                    this.setCurrentPosts();
                    this.setPagination();
                })

            },
            getEmpInfo(id) {    //  id this.employee.emp_id . : mounted()  .
                http
                    .post("/mypage/" + id)
                    .then(response => {
                        //   employee  .
                        this.dep_id = response.data.dep_id;
                    })
                    .catch(e => {
                        /* eslint-disable no-console */
                        console.log(e);
                    });
            },
            changePage(pageNum) {
                this.page = pageNum;
                this.setCurrentPosts();
            },
            setCurrentPosts() {
                this.currentPosts = [];
                let j = (this.page-1) * this.countList;
                for(let i=0; i<this.countList && j < this.dep_board.length; i++) {
                    this.currentPosts[i] = this.dep_board[j];
                    j++;
                }

            },
            setPagination() {
                this.count = this.dep_board.length;
                this.totalPage = this.count / this.countList; //   
                if(this.count % this.countList > 0){
                    this.totalPage = Math.ceil(this.totalPage); //   totalPage    ()
                }

                if(this.totalPage < this.page){
                    this.page=this.totalPage;
                }
                for(let i=0; i<this.totalPage; i++) {
                    this.totalPages[i] = i+1;
                }
                this.startPage = ((this.page -1)/this.countPage) * this.countPage +1; //  
                if(this.totalPage < 5) {
                    this.endPage = this.totalPage;  // endPage  totalPage  
                } else {
                    this.endPage = this.startPage + this.countPage -1; //  
                }
                //    
                this.currentPages = [];
                let j = this.startPage-1;
                for(let i=0; i<=(this.endPage-this.startPage) && j <
                this.totalPage; i++) {
                    this.currentPages[i] = this.totalPages[j];
                    j++;
                }
            },
            prev() {
                if(this.startPage != 1) {
                    this.startPage = this.startPage -5;
                    this.page = this.startPage;
                    if(this.totalPage < 5) {
                        this.endPage = this.totalPage;  // endPage  totalPage  
                    } else {
                        this.endPage = this.startPage + this.countPage -1; //  
                    }

                    this.currentPages = [];
                    let j = this.startPage-1;
                    for(let i=0; i<=(this.endPage-this.startPage) && j < this.totalPage; i++) {
                        this.currentPages[i] = this.totalPages[j];
                        j++;
                    }
                    this.setCurrentPosts();
                }
            },
            next() {
                if(this.endPage < this.totalPage) {
                    this.startPage = this.endPage +1;
                    this.page = this.startPage;
                    if(this.totalPage < 5) {
                        this.endPage = this.totalPage;  // endPage  totalPage  
                    } else {
                        this.endPage = this.startPage + this.countPage -1; //  
                    }

                    this.currentPages = [];
                    let j = this.startPage-1;
                    for(let i=0; i<=(this.endPage-this.startPage) && j < this.totalPage; i++) {
                        this.currentPages[i] = this.totalPages[j];
                        j++;
                    }
                    this.setCurrentPosts();
                }
            },
            gotoStart() {
                this.changePage(1);
                this.setPagination()
            },
            gotoEnd() {
                let pack = Math.ceil(this.totalPage/this.countPage) // ()
                for(let i=0; i<pack && pack>0; i++){
                    this.next()
                    this.changePage(this.totalPage);
                }
            },
            isSelected(index) {
                /*  class   return  */
                if (index == (this.page-1)%this.countPage) {
                    return true
                } else {
                    return false
                }
            }


        },
        mounted() {
            // mounted     
            if (sessionStorage.length > 0) { //  sessionStorage    (  sessionStorage   )
                this.empID = sessionStorage.getItem("login_id"); //    empID     
                this.getEmpInfo(this.empID);    //        
                this.setBoard();

            } else {
                this.$router.push("/");
            }
        },
        // router    parameter     .
        //  parameter             .
        beforeRouteUpdate (to, from, next) {
            this.board = to.params;
            this.board_name = to.params.board_name;
            this.BoardAll();
            next();
        },


    };
</script>

<style>
    .AllPost{
        width: 120%;
    }
    .searchbar{
        position: absolute;
        right: 13%;
        top: 15%;
    }

</style>