<template>
    <div class="subMenu">
        <ul class="nav nav-pills flex-column">
            <li class="list-group-item addCalendarList libgclr">
                <button class="btn btn-lg btn-link" @click="addSchedule()"> </button>
            </li>
            <li class="list-group-item libgclr">
                <label class="col-form-label col-form-label calendarLabel" for="inputSmall"> </label><br>
                <div style="width: 100%">
                    <input type="color" class="inputColor" v-model="calendarInput.cal_color">
                    <input class="form-control form-control-sm addInput" type="text" placeholder="  " id="inputSmall" v-model="calendarInput.cal_name">

                    <button class="btn btn-lg btn-link addBtn" @click="addCalendar()">+</button>
                </div>
            </li>
            <li class="list-group-item libgclr">
                <div v-if="calendarList">
                    <label class="col-form-label col-form-label calendarLabel"> </label><br>
                    <label class="form-check-label category" v-for="(calendar, index) in calendarList" :key="index">
                        <input class="checkBox" type="checkbox" :value="calendar.calendar_id" v-model="checkedCalendars" checked="calendar_chk">

                            <span style="font-size: 15px" :style="{color: calendar.calendar_color}"></span>&nbsp;
                            {{calendar.calendar_name}}
                    </label><br><br>

                    <button class="btn btn-info btn-sm pushCalBtn" @click="modifyCalendar()"></button>
                    <button class="btn btn-info btn-sm pushCalBtn" @click="deleteCalendarList()"></button>
                </div>
            </li>

        </ul>
    </div>
</template>

<script>
    import http from "../../http-common";
    import RegisterPopup from "./RegisterPopupModal";
    import ScheduleModifyModal from "./ScheduleModifyModal";

export default {
    data() {
        return {
            emp_id: "",         // 
            calendarInput : {   //   Input
              cal_name: "",
              cal_color: "#ffffff"
            },
            calendarList: [],         //   
            checkedCalendars : [],    //   
            modi_flag : [],         //      flag
            modi_cal_input : [],
            modi_cal_color : []
        }
    },
    methods : {
        addSchedule() { //   modal  
            this.$modal.show(RegisterPopup, {
                registerData : 'data',
                modal : this.$modal,
            },
            {
                 name: 'dynamic-modal',
                 width:'400px',
                 height: '600px',
                 draggable: true
            });
        },
        addCalendar() { //  
            let sche_data = {
                calendar_name : this.calendarInput.cal_name,
                calendar_color: this.calendarInput.cal_color
            }
            if(this.calendarInput.cal_name === "" || this.calendarInput.cal_color === "") {
                return;
            } else {
                http
                    .post("/calendar/add/" + this.emp_id, sche_data)
                    .then(response=> {
                        this.getCalendarList();
                    })
                    .catch(e => {
                        /* eslint-disable no-console */
                        console.log(e);
                    });
            }

        },
        getCalendarList() { //   
            http
                .get("/calendar/list/" + this.emp_id)
                .then(response=> {
                    /* eslint-disable no-console */
                    this.calendarList = response.data;
                    for(let i=0; i<this.calendarList.length; i++) {
                        this.modi_flag[i] = false;
                    }
                })
                .catch(e => {
                    /* eslint-disable no-console */
                    console.log(e);
                });
        },
        modifyCalendar() {  //   Modal  
            this.$modal.show(ScheduleModifyModal, {
                    checkedCalendars: this.checkedCalendars,    //   calendar_id 
                    modal: this.$modal,
                },
                {
                    width: '500px',
                    height: '400px',
                    draggable: true
                });

            this.checkedCalendars = []; //    
            // this.getCalendarList(); //    
        },
        deleteCalendarList() {  //     
            if(this.checkedCalendars.length !=0) {
                let flag = confirm(" ?");

                if(flag == true) {
                    if(this.checkedCalendars.length !=0) {
                        http
                            .delete("/calendar/list/" + this.emp_id +"?calendar_id=" + this.checkedCalendars)
                            .then(response=> {
                                /* eslint-disable no-console */
                                this.checkedCalendars = []; //    
                            })
                            .catch(e => {
                                /* eslint-disable no-console */
                                console.log(e);
                            });
                    }
                }
            }

        }
    },
    mounted() {
        // mounted     
        if (sessionStorage.length > 0) { //  sessionStorage    (  sessionStorage   )
            this.emp_id = sessionStorage.getItem("login_id");
            this.getCalendarList();
        }else {
            this.$router.push("/");
        }
    }
};
</script>

<style scoped>
    .subMenu {
        min-width: 200px;
    }
    button{
        width:100%;
    }
    .category{
        width: 90%;
        text-align: left;
        font-size: 13px;
        margin: auto 20px;
        padding-right: 10px;
    }
    .calendarLabel {
        padding-top: 0;
        padding-bottom: 10px;
    }
    .inputColor {
        margin-top: 5px;
        float: left;
    }
    .addInput {
        width: 75%;
        float: left;
        margin-left: 5px;
        margin-right: 2px;
    }
    .addBtn {
        width: 5%;
        float: left;
        padding: 0;
    }
    .checkBox {
        margin-top: 5px;
        margin-right: 5px;
    }
    .pushCalBtn {
        width: 50px;
    }
    /* input color   */
    input[type="color"] {
        cursor: pointer;    /*      */
        -webkit-appearance: none;
        width: 18px;
        height: 17px;
        border-radius: 50%;
    }
    input[type="color"]::-webkit-color-swatch-wrapper {
        padding: 0;
    }
    input[type="color"]::-webkit-color-swatch {
        box-sizing: border-box;
        border-radius: 50%;
    }
    .addCalendarList {
        height: 60px;
        vertical-align: middle;
        padding-top: 8px;
    }
</style>
