<template>
    <div class="mainWorking">
        <div class="alignCenter">
            <Clock></Clock>
            :{{this.working.workingIn}}<br>
            :{{this.working.workingOut}}<br>
            :{{this.working.workingTime}}<br>
            <button v-if="!this.working.workingIn" v-on:click="saveWorkingIn()"
                    class="btn btn-outline-danger btn-working">
            </button>
            <button v-else class="btn btn-danger disabled"></button>

            <button v-if="!this.working.workingOut" v-on:click="saveWorkingOut()"
                    class="btn btn-outline-info btn-working">
            </button>
            <button v-else class="btn btn-info disabled"></button>
        </div>
    </div>
</template>

<script>
    import http from "../../http-common";
    import Clock from "../working/Clock";

    export default {
        name: "WorkingSubMenu",
        data() {
            return {
                working: {
                    empId: "",
                    workingDate: "",
                    workingIn: "",
                    workingOut: "",
                    workingTime: "",
                }
            };
        },// End - data
        components: {
            Clock: Clock
        },
        methods: {
            /* eslint-disable no-console */
            readWorkingToday() {//   , mounted()  .
                http
                    .get("/working/readToday/" + this.working.empId)
                    .then(response => {
                        this.working = response.data;
                        console.log("read" + response.data);
                    })
                    .catch(e => {
                        console.log(e);
                    });
            },// End - readWorkingToday()
            saveWorkingIn() {//    DB   
                http
                    .get("/working/saveIn/" + this.working.empId)
                    .then(response => {
                        this.working.workingIn = response.data;
                        console.log(response.data);
                    })
                    .catch(e => {
                        console.log(e);
                    });
            },// End - saveWorkingIn()
            saveWorkingOut() {//     DB   
                http
                    .get("/working/saveOut/" + this.working.empId)
                    .then(response => {
                        this.working.workingOut = response.data;
                        console.log(response.data);
                        this.calcWorkingTime();
                    })
                    .catch(e => {
                        console.log(e);
                    });
            },// End - saveWorkingOut()
            saveWorkingTime() {//    DB   
                http
                    .get("/working/saveTime/" + this.working.empId + "?time=" + this.working.workingTime)
                    .then(response => {
                        this.working.workingTime = response.data;
                        console.log(response.data);
                    })
                    .catch(e => {
                        console.log(e);
                    });
            },// End - saveWorkingTime()
            calcWorkingTime() {//     
                let wit = new Date(Date.parse("0001-01-01 " + this.working.workingIn));//  Date 
                let wot = new Date(Date.parse("0001-01-01 " + this.working.workingOut));//  Date 

                wot.setHours(wot.getHours() - wit.getHours());//   
                wot.setMinutes(wot.getMinutes() - wit.getMinutes());//   
                wot.setSeconds(wot.getSeconds() - wit.getSeconds());//   

                this.working.workingTime = wot.toString().substr(16, 8); //       
                this.saveWorkingTime();// DB    
            },// End - calcWorkingTime()
            /* eslint-enable no-console */
        },// End - methods
        mounted() {
            // mounted     
            if (sessionStorage.length > 0) { //  sessionStorage    (  sessionStorage   )
                this.working.empId = sessionStorage.getItem("login_id");// sessionStorage  
                this.readWorkingToday();//   
            }
        }// End - mounted()
    };// End - export default
</script>

<style>
    .alignCenter {
        text-align: center;
    }
    .mainWorking{
        width: 15%;
        height: 50%;
        left: 15px;
        top: 50%;
        position: absolute;
        border: 1px solid gray;
    }
</style>
