<template>
    <div class="root2">
    <div v-if="this.post">
        <div>
            <subMenu></subMenu>
        </div>
            <h4 style="margin: 20px; font-weight: bold">-{{this.post.board_name}} -</h4>
        <hr style="margin: 10px">
        <!---->
        <div v-if="flex">
            <div class="subjectbox" style="font-weight: bold">
                <label > : </label> &nbsp;&nbsp;&nbsp;&nbsp;{{this.post.created}}
            </div>
            <div class="subjectbox" style="font-weight: bold">
                <label > : </label> &nbsp;&nbsp;&nbsp;&nbsp;{{this.post.post_writer}}
            </div>

            <div class="subjectbox" style="font-weight: bold">
                <label >&nbsp;&nbsp;&nbsp;&nbsp; :</label> &nbsp;&nbsp;&nbsp;&nbsp;{{this.post.post_subject}}
            </div>

            <div class="card border-dark mb-5" style="max-width: 40rem;">
                <div class="card-body">
                    <h4 class="card-title"></h4>
                    <p class="card-text" >{{this.post.post_content}}</p>
                </div>
             </div>
              <div v-if="chkWriter()">
            <button class="btn btn-danger" type="button" @click="deletePost"> </button>
            <button class="btn btn-success" type="button" @click="modify"> </button>

             </div>
        </div>
        <!-- div  -->

        <div v-else class>
         <label></label>
            <input type="text" class="form-control" id="subject"
                   required v-model="post.post_subject" name="subject">
            <label></label>
            <textarea class="form-control textArea" rows="15" required v-model="post.post_content">{{post.post_content}}</textarea><br>

            <button class="btn btn-success" type="button" @click="updatePost"></button>
        </div>
        <div class="returnbtnbox">
            <button class="btn btn-primary" type="button" @click="goBack"></button>
        </div>
        <br>

    </div>

    <div v-else>
        <br/>
        <p>Please click on a Board...</p>
    </div>
    </div>
</template>

<script>

    import http from "../../http-common";
    import PostSubmenu from "./PostSubmenu";

    export default {
        name: "post",
        data() {
            return{
                 flex: true, //     ==  false 
                 emp_id: "",
                 emp_name:""
            }
        },
        components: {
            subMenu: PostSubmenu
        },
        props: ["post"],
        methods: {
            goBack(){
                history.go(-1);
            },

            //   
            deletePost() {
                http
                    .delete("/pst/delete/" + this.post.post_id)
                    .then(response => {
                        console.log(response.data);
                        this.$router.push('/pst');
                    })
                    alert(".")
                    .catch(e => {
                        console.log(e);
                    });
            },
            chkWriter(){
                if(this.emp_name == this.post.post_writer){
                    return true;
                }else{
                    return false;
                }

            },


            modify() {
                this.flex=false
            },
            //  
            updatePost() {
                let postData = {
                    post_subject : this.post.post_subject,
                    post_content : this.post.post_content,
                };
                http
                    .put("/pst/update/"+this.post.post_id, postData)
                    .then(response => {
                        console.log("");
                        alert("")
                        this.$router.push("/pst/posts")
                    })
                    .catch(e => {
                        console.log(e);
                    });

            },
            getEmpInfo(id) {    //  id this.employee.emp_id . : mounted()  .
                http
                    .post("/mypage/" + id)
                    .then(response => {
                        //   employee  .
                        this.emp_name = response.data.emp_name;
                    })
                    .catch(e => {
                        /* eslint-disable no-console */
                        console.log(e);
                    });
            }


        },
        mounted() {
            // mounted     
            if (sessionStorage.length > 0) { //  sessionStorage    (  sessionStorage   )
                this.emp_id = sessionStorage.getItem("login_id"); //    empID     
                this.getEmpInfo(this.emp_id);
            } else {
                this.$router.push("/");
            }
        },


    };
</script>
<style scoped>
    .sidelist {
        position: fixed;
        left: 0;
    }
    .card {
        position: center;
        text-align: left;
        left: 400px;
        height: 400px;
    }
    .form-control {
        width: 60%;
        margin : auto;
    }
    .root2{
        width: 800px;
        border: silver solid 0px;
        margin-left: 25%;
        padding: 20px 0 50px 0;
        position: static;
        background-color: rgba(239,239,239,0.2);
        left: 24%;
        float: left;
    }
    .card{
        margin: 40px auto;
        left: 0;
        width: 80%;
    }
    .subjectbox{
        margin: 10px auto;
        left: 0;
        width: 80%;
        border: silver solid 0px;
        text-align: left;
    }
    .returnbtnbox{
        position: absolute;
        bottom: -9.5%;
        left: 67%;
    }

</style>