<template>
    <div class="subMenu">

        <ul class="nav nav-pills flex-column">
            <div style="font-weight: bold; margin: 8%; font-size: 20px; text-align: center">
                
            </div>
            <router-link to="/pst">
                <li class="list-group-item d-flex justify-content-between align-items-center libgclr" style="font-weight: bold">
                     
                </li>
            </router-link>
            <router-link to="/dep_pst">
            <li class="list-group-item d-flex justify-content-between align-items-center libgclr" style="font-weight: bold">
                 
            </li>
        </router-link>
            <button type="button" class="btn btn-primary btn-lg" @click="getBoard">  </button>

                <li class="list-group-item d-flex justify-content-between align-items-center libgclr"
                    v-for="(board, index) in boards" :key="index">
                    <router-link :to="{
                       name: 'DepPage',
                       params:{board:board, board_name:board.board_name}
                    }">
                        <span>{{board.board_name}}</span>
                    </router-link>
                </li>
        </ul>
        <router-link to="/dep_pst/newBoard">
            <button type="button" class="btn btn-success"> </button>
        </router-link>
        <router-link to="/dep_pst/DeleteBoard">
            <button type="button" class="btn btn-danger"> </button>
        </router-link>
    </div>
</template>

<script>
    import http from "../../http-common";

    export default {
        name: "board",
        data() {
            return {

                submitted: false,
                empID: "",
                dep_id: "",
                board_name:"",
                boards:[]

            };
        },

        methods: {
            /* eslint-disable no-console */
            //    
            getBoard(){
                http
                .get("/board/getBoard/" + this.dep_id)
                .then(response =>{
                    this.boards=response.data;
                })

            },


            backlist(){
                history.go(-1);
            },

            getEmpInfo(id) {    //  id this.employee.emp_id . : mounted()  .
                http
                    .post("/mypage/" + id)
                    .then(response => {
                        //   employee  .
                        this.dep_id = response.data.dep_id;
                    })
                    .catch(e => {
                        /* eslint-disable no-console */
                        console.log(e);
                    });
            },
            writeCheck() {
                let write_chk = location.href.indexOf("dep_pst");  // dep_pst    0()  ,  -1()
                console.log(write_chk);
                if(write_chk==-1) {
                    //     
                    this.post_dep_id = 0;   //  post_dep_id  0
                } else {
                    this.getEmpInfo(this.empID);    // empID   : dep_id 

                }
            }
        },
        mounted() {
            if (sessionStorage.length > 0) { //  sessionStorage    (  sessionStorage   )
                this.empID = sessionStorage.getItem("login_id"); //    empID     
                // this.getName(this.empID);   // empID    
                this.getEmpInfo(this.empID);

            } else {
                this.$router.push("/");
            }

        },
        updated() {

                this.writeCheck();
        }
    };
</script>

