<template>
    <div>
    <h3 style="margin: 20px; font-weight: bold"> </h3>
    <div class="root">
        <div class="submitform">
            <div>
                <subMenu></subMenu>
            </div>

            <div v-if="!submitted"  class="ppo">

                <div class="form-group form-inline " >
                    <label for="writer" class="labelfont"></label>
                    <input type="text" class="form-control col-sm-9" id="writer" required v-model="survey.survey_writer" name="writer" placeholder="" readonly>
                </div>

                <div class="form-group form-inline">
                    <label for="start_date" class="labelfont"></label>
                    <input type="date" class="form-control col-sm-9" id="start_date" required v-model="survey.start_date" name="start_date" placeholder=" ">
                </div>
                <div class="form-group form-inline">
                    <label for="end_date" class="labelfont"></label>
                    <input type="date" class="form-control col-sm-9" id="end_date" required v-model="survey.end_date" name="end_date" placeholder=" ">
                </div>

                <div class="form-group form-inline">
                    <label for="subject" class="labelfont"></label>
                    <input type="text" class="form-control col-sm-9" id="subject" required v-model="survey.survey_subject" name="subject" placeholder=" ">
                </div>
                <div class="form-group form-inline">
                    <label for="question" class="labelfont">&nbsp;&nbsp;&nbsp; </label>
                    <input type="text" class="form-control col-sm-9" id="question" required v-model="survey.survey_question" name="question" placeholder="  ">
                </div>
                <div class="form-group form-inline">
                    <label for="answer1" class="labelfont"> 1</label>
                    <input type="text" class="form-control col-sm-9" id="answer1" required v-model="survey.answer1" name="answer1" placeholder=" 1">
                </div>

                <div class="form-group form-inline">
                    <label for="answer2" class="labelfont"> 2</label>
                    <input type="text" class="form-control col-sm-9" id="answer2" required v-model="survey.answer2" name="answer2" placeholder=" 2">
                </div>
                <div class="form-group form-inline">
                    <label for="answer3" class="labelfont"> 3</label>
                    <input type="text" class="form-control col-sm-9" id="answer3" required v-model="survey.answer3" name="answer3" placeholder=" 3">
                </div>

                <div class="form-group1 form-inline">
                    <label for="content" class="labelfont"> </label>
                    <textarea class="form-control col-sm-10" id="content" rows="10" required v-model="survey.survey_detail" name="content" placeholder="1000  "></textarea>
                </div>
                <br>

                <button v-on:click="saveSurvey" class="btn btn-success" style="font-weight: bold;" > </button>
                <button v-on:click="cnlth" class="btn btn-danger" style="font-weight: bold;" ></button>
            </div>
        </div>
    </div>
    </div>
</template>

<script>
    import http from "../../http-common";
    import PostSubmenu from "../posts/PostSubmenu";
    import SurveySubmenu from "./SurveySubmenu";


    export default {
        name: "addsurvey",
        data() {
            return {
                survey: {
                    survey_id:"",
                    survey_writer:"",
                    survey_subject:"",
                    survey_detail:"",
                    survey_question:"",
                    answer1:"",
                    answer2:"",
                    answer3:"",
                    start_date:"",
                    end_date:""

                },
                submitted: false,
                empID: ""
            };
        },
        components: {
            subMenu: SurveySubmenu
        },

        methods: {
            //    
            cnlth(){
                this.$router.push({
                    path:'/survey/'
                })
            },
            /* eslint-disable no-console */
            // form    data   controller   
            saveSurvey() {
                var data = {
                    survey_id: this.survey.survey_id,
                    survey_writer: this.survey.survey_writer,
                    survey_subject: this.survey.survey_subject,
                    survey_question: this.survey.survey_question,
                    survey_detail: this.survey.survey_detail,
                    start_date: this.survey.start_date,
                    end_date: this.survey.end_date,
                    answer1: this.survey.answer1,
                    answer2: this.survey.answer2,
                    answer3: this.survey.answer3
                };
                    if(data.survey_subject==null || data.survey_subject==""){
                        alert("  ");
                    }else if(data.survey_question==null ||data.survey_question==""){
                        alert("  ");
                    }else if(data.survey_detail ==null || data.survey_detail==""){
                        alert("  ");
                    }else if(data.start_date ==null || data.start_date == ""){
                        alert("  ");
                    }else if(data.end_date == null || data.end_date== ""){
                        alert("  ");
                    }else if(data.answer1 ==null || data.answer1==""){
                        alert(" 1  ");
                    }else if(data.answer2 ==null || data.answer2==""){
                        alert(" 2  ");
                    }else if(data.answer3 == null || data.answer3==""){
                        alert(" 3  ");
                    }else{
                http
                    .post("/survey/addsurvey", data)
                    .then(response => {
                        this.survey.survey_id = response.data.survey_id;
                        console.log(response.data);
                    })
                    .catch(e => {
                        console.log(e);
                    });
                this.submitted = true;
                alert("   !");
                this.$router.push({
                    path:'/survey'
                })
                    }

            },
            backlist(){
                this.$router.push({
                    path:'/survey'
                })
            },
            getName(login_id) {
                /*      */
                http
                    .post("/login/name/" + login_id)
                    .then(response => {
                        this.survey.survey_writer = response.data; // survey_writer     .
                    })
            } // End - getName()
        },
        mounted() {
            // mounted     
            if (sessionStorage.length > 0) { //  sessionStorage    (  sessionStorage   )
                this.empID = sessionStorage.getItem("login_id"); //    empID     
                this.getName(this.empID);   // empID    
            } else {
                this.$router.push("/");
            }
        }
    };
</script>

<style scoped>
    .submitform{
         max-width: 800px;
         margin: auto;
         border: solid lightgray 0px ;

     }
    .form-group{
        width: 700px !important;
    }
    .sidelist {
        width: 15%;
        left: 0;
    }
    .ppo{
        text-align: center;
        max-width: 650px;
        width: 100%;
        margin: 0 auto;

    }
    .root{
        width: 800px;
        border: silver solid 1px;
        margin-left: 25%;
        padding: 20px 0 50px 0;
        position: static;
        left: 24%;

    }
    .btn btn-success{
        display: none;
    }
    .labelfont{
        font-weight: bold;
        margin-right: 10px;
    }

</style>