<template>
    <div class="root3">
        <div>
            <subMenu></subMenu>
        </div>
    <div>
        <h4 style="margin: 20px; font-weight: bold"></h4>

    <div v-if="this.survey" class="rootbox">



        <hr style="width: 80%; margin: 20px 60px 20px 75px;">
        <!---->

        <div class="subjectbox_survey" style="font-weight: bold">
            <label > : </label> &nbsp;&nbsp;&nbsp;&nbsp;{{this.survey.survey_writer}}
        </div>
        <div class="subjectbox_survey" style="font-weight: bold">
            <label > : </label> &nbsp;&nbsp;&nbsp;&nbsp;{{time(this.survey.start_date)}}
        </div>
        <div class="subjectbox_survey" style="font-weight: bold">
            <label > : </label> &nbsp;&nbsp;&nbsp;&nbsp;{{time(this.survey.end_date)}}
        </div>


        <hr  style="width: 80%; margin-bottom: 20px;">

            <div class="qstbox">
                <label style="font-weight: bold; font-size: 25px;">  &nbsp;&nbsp;&nbsp;&nbsp;{{this.survey.survey_question}}</label>
            </div>

        <div class="progressbox">
            1 :   {{this.survey.answer1 }}({{width1}}%)&nbsp;&nbsp;<input v-if="!flag" type="radio" id="one" name="a" :value="1" v-model="survey.answer">
            <div class="progress">                                  <!--:style="{ color: activeColor, 'font-size': fontSize + 'px' }"-->
                <div class="progress-bar bg-info" role="progressbar" :style="{width: width1+'%'}"></div>
            </div><br>

          2 :  {{this.survey.answer2 }}({{width2}}%) &nbsp;&nbsp; <input v-if="!flag" type="radio" id="two" name="a" :value="2" v-model="survey.answer"><br>

            <div class="progress">
                <div class="progress-bar bg-warning" role="progressbar" :style="{width: width2+'%'}"></div>
            </div><br>

           3 :  {{this.survey.answer3 }}({{width3}}%)&nbsp;&nbsp; <input v-if="!flag" type="radio" id="three" name="a" :value="3" v-model="survey.answer"><br>

            <div class="progress">
                <div class="progress-bar bg-danger" role="progressbar" :style="{width: width3+'%'}"></div>
            </div> <br>
            <small class="form-text text-muted" id="fileHelp">     .</small>
            <small class="form-text text-muted" id="hihi">    .</small>

        </div>
        <br>
        <span style="font-weight: bold">   : {{this.choice.length}} </span><br>
            <br><br>
            <button v-if="!flag" class="btn btn-success" type="button" @click="savevote"></button>
            <button class="btn btn-primary" type="button" @click="goBack"></button>


            <button v-if="empID==10002"class="btn btn-danger" type="button" @click="deletePost"></button>
        </div>
        <!-- div  -->
        <div v-else>
            <label></label>
            <input type="text" class="form-control" id="subject"
                   required v-model="survey.survey_subject" name="subject">
            <label></label>
            <textarea class="form-control textArea" rows="15" required v-model="survey.survey_detail">{{survey.survey_detail}},


            </textarea><br>

<!--            <button class="btn btn-success" type="button" @click="updatePost"></button>-->
        </div>

    </div>
    </div>
</template>

<script>
    import http from "../../http-common";
    import SurveySubmenu from "./SurveySubmenu";
    import VueApexCharts from 'vue-apexcharts';
    export default {
        name: "survey",

        data() {

            return {
                flex: "",//     ==  false 
                flag:"",
                empID: "", //   empID .
                //2020-01-23   
                // selection: [],
                emp_id:[],
                choice:[],
                width1 : 0, //  1   %
                width2 : 0, //  2   %
                width3 : 0, //  3   % - width1 ~ width3    100%
                s1:0,
                s2:0,
                s3:0,
                // select: 0

            }

        },
        components: {
            subMenu: SurveySubmenu
        },
        props: ["survey"],
        methods: {
            checkwidth(){
              if(this.width1==null || this.width1==""){
                  this.width1.toString(0);
              }
            },
            time(date){
                var moment=require("moment");

                return moment(date).format("YYYY MM DD");
            },

            goBack(){
                  history.go(-1);
            },
            //       0,1,2  .
            savevote(){
                var vote ={
                    selection:this.survey.answer,
                    emp_id: this.empID,
                    survey_id: this.survey.survey_id
                };
                http
                .put("/survey/vote",vote)
                .then(response => {
                    // this.survey.answer =response.data.answer;
                    console.log(" ?"+response.data.selection);
                    console.log(vote+"selection")
                    // console.log(this.survey.answer)
                    alert(" .")
                    this.$router.push('/survey');


                })

            },

            //2020-01-23  
            getvote(){
                console.log("  : " + this.survey.survey_id)
                http
                .get("/survey/getvote/"+this.survey.survey_id)
                .then(response => {
                    this.choice = response.data;
                    console.log("emp_id>>>>>>>>>>>>>>>"+this.choice.emp_id);
                    // choice   this.choice.length :   2
                    // for    choice   selection   :  2 2 100%
                    // selection  1   width1 ....width2, width3  
                    // width1, width2, width3       
                    for(let i=0; i<this.choice.length;i++){
                        this.emp_id[i]=this.choice[i].emp_id;
                        console.log(" emp>>>>>>>>>>>>>>"+this.emp_id);
                    }

                   this.checkVoter();

                    for(let i=0; i<this.choice.length;i++){
                        console.log(this.choice[i].selection + "selection");
                        if(this.choice[i].selection==1){

                            console.log(i + "for I " )
                            this.s1++;
                            console.log(this.s1 + "s1")
                        }else if(this.choice[i].selection===2){
                            this.s2++;
                        }else{
                            this.s3++;
                        }
                    }
                        this.width1=this.s1/this.choice.length * 100;
                        this.width2=this.s2/this.choice.length * 100;
                        this.width3=this.s3/this.choice.length * 100;

                });

            },
            checkVoter(){
                for(let i=0; i<this.emp_id.length; i++) {
                    if(this.empID == this.emp_id[i]) {
                        this.flag = true;//         flag true 
                        alert("   !")
                    }
                }
            },
            deletePost() {
                http
                    .delete("/survey/deletesurvey/" + this.survey.survey_id)
                    .then(response => {
                        console.log(response.data);
                        // this.$emit("refreshData");
                        this.$router.push('/survey');
                    })
                alert(" .");
            },

        },
        mounted() {
            // mounted     
            if (sessionStorage.length > 0) { //  sessionStorage    (  sessionStorage   )
                this.empID = sessionStorage.getItem("login_id");
                this.getvote();

            } else {
                this.$router.push("/");
            }
        },
        updated() {

        }
    };
</script>
<style>
    /*.sidelist {*/
    /*    position: fixed;*/
    /*    left: 0;*/
    /*}*/
    .card {
        position: center;
        text-align: left;
        left: 400px;
        height: 400px;
    }
    .progress {
        width: 60%;
        margin-left: 20%;
        height: 40px;
    }
    .root3{
        width: 800px;
        border: silver solid 1px;
        margin-left: 20% !important;
        padding: 20px 0 50px 0;
        position: static !important;
        left: 24%;

    }
    .subjectbox_survey{
        margin: 10px auto;
        left: 0;
        width: 80%;
        border: silver solid 0px;
        text-align: left;
    }
    .progressbox{
        border: #dddddd solid 1px;
        width: 80%;
        padding: 20px;
        margin-left: 25%;
    }

    /*.qstbox {*/
    /*    position: absolute;*/
    /*    left: 10%;*/
    /*}*/

</style>