<template>
    <div class="submitform">
        <table class="table table-hover">

            <tr>
                <th></th>
                <td>{{employee.emp_id}}</td>
            </tr>
            <tr>
                <th></th>
                <td><input type="text" placeholder=" " required v-model="employee.name"></td>
            </tr>
            <tr>
                <th></th>
                <td><input type="text" placeholder=" " required v-model="employee.dep_id"></td>
            </tr>
            <tr>
                <th></th>
                <td><input type="text" placeholder=" " required v-model="employee.position"></td>
            </tr>
            <tr>
                <th></th>
                <td><input type="text" placeholder=" " required v-model="employee.extension_number"></td>
            </tr>
            <tr>
                <th></th>
                <td><input type="text" placeholder=" " required v-model="employee.hire_date"></td>
            </tr>
            <tr>
                <th></th>
                <td><input type="text" placeholder=" " required v-model="employee.emp_email"></td>
            </tr>
            <tr>
                <th></th>
                <td><input type="text" placeholder=" " required v-model="employee.phone_number"></td>
            </tr>
        </table>
        <button class="btn btn-primary btn-lg" @click="updateEmployees(employee.emp_id)"></button>
        <button class="btn btn-primary btn-lg" @click="deleteEmployees()"></button>
    </div>

</template>

<script>
    import http from "../../http-common";
    export default {

        name: "EmployeesMgmDetail",
        props: ["employee"],
        data() {
            return {
                employee: {
                    emp_id: "",
                    emp_pw: "",
                    emp_name: "",
                    emp_email: "",
                    position: "",
                    hire_date: "",
                    extension_number: "",
                    phone_number: "",
                    dep_id: ""
                },
                dep_name: "", //   
                emp_img_url: "" //   
            };
        },
        created() { //   
            this.getEmpInfo()
        },
        watch:{'$route':'getEmpInfo'},  //    
        methods:{

            getDep_Name(dep_id) {
                http
                    .post("/dep/" + dep_id)
                    .then(response => {
                        this.dep_name = response.data;
                        console.log(this.dep_name+"");
                    })
                    .catch(e => {
                        console.log(e);
                    });
            }, //   
            updateEmployees(id) {
                let data = {
                    name: this.employee.name,
                    dep_id: this.employee.dep_id,
                    position:this.employee.position,
                    extension_number: this.employee.extension_number,
                    hire_date: this.employee.hire_date,
                    emp_email: this.employee.emp_email,
                    phone_number : this.employee.phone_number
                }

                if (data.name==""|data.dep_id==""|data.position==""|data.extension_number==""|
                    data.hire_date==""|data.emp_email==""|data.phone_number == ""){ //    
                    alert("  !");
                } else {
                    http
                        .put("/employees/update/" + id, data) // UpfficeBack MyPageController  .
                        .then(response => {
                            if (response.data == 1) { //  1 1   ,   !
                                alert(" !");

                               this.$router.push("/manager")
                            }
                        })
                        .catch(e => {
                            console.log(e);
                        });
                }
            }, // End - updatePhone() :   ( , )
            deleteEmployees(){
                http
                .delete("/employees/employees/"+ this.employee.emp_id)
                .then(response=>{
                  console.log(response.data);
                  this.$emit("refreshData");
                  this.$router.push('/manager/employees');
                })
                .catch(e=>{
                   console.log(e);
                });

            }, //END- delete
        },
        mounted() {
            // mounted     
            if (sessionStorage.length > 0) { //  sessionStorage    (  sessionStorage   )
                this.id = this.employee.emp_id;
                console.log(this.id);
                this.getEmpInfo(this.employees.emp_id); //   
                this.getDep_Name(this.employee.dep_id);
            }else {
                this.$router.push("/manager");
            }
        } // End - mounted()
    }
</script>

