<template>
    <div class="list row">
        <subMenu></subMenu>
        <div class="col-md-12">
            <h4></h4><button v-on:click="showModal()" class="btn btn-outline-success btn-lg"></button>
            <table class="table table-hover">
                <thead>
                <tr class="table-primary">
                    <td></td>
                    <td> </td>
                    <td> </td>
                    <td> </td>
                </tr>
                </thead>
                <tr>
                    <td>{{this.name}}</td>
                    <td>{{this.totalAnnual}}</td>
                    <td>{{this.usedAnnual}}</td>
                    <td>{{this.leftAnnual}}</td>
                </tr>
            </table>
        </div>
        <div class="col-md-12">
            <h4>  </h4>
            <table class="table table-hover">
                <thead>
                <tr class="table-primary">
                    <td></td>
                    <td> </td>
                </tr>
                </thead>
                <tr v-for="(annual, index) in annuals" :key="index">
                    <td>{{index+1}}</td>
                    <td>{{annual}}</td>
                </tr>
            </table>
        </div>
        <modals-container />
    </div>
</template>

<script>
    import http from "../../http-common";
    import WorkingSubMenu from "./WorkingSubMenu";
    import Modal from "./Modal.vue";

    export default {
        name: "annual-detail",
        data() {
            return {
                empId: "",
                name: "",
                totalAnnual: 0,
                usedAnnual: 0,
                leftAnnual: 0,
                annuals: []
            };
        },
        components: {
            subMenu: WorkingSubMenu,
            Modal: Modal
        },
        methods: {
            /* eslint-disable no-console */
            readAnnuals() {//     
                http
                    .get("/working/annuals/" + this.empId)
                    .then(response => {
                        this.annuals = response.data; // JSON are parsed automatically.
                        console.log(response.data);
                    })
                    .catch(e => {
                        console.log(e);
                    });
            },// End - readAnnuals()
            readTotalAnnual(){//   
                http
                    .get("/working/totalAnnuals/" + this.empId)
                    .then(response => {
                        this.totalAnnual = response.data; // JSON are parsed automatically.
                        console.log(response.data);
                    })
                    .catch(e => {
                        console.log(e);
                    });
            },// End - readTotalAnnual()
            setLeftAnnual(){//    
                this.usedAnnual=this.annuals.length;
                this.leftAnnual=this.totalAnnual-this.usedAnnual;
            },// End - setLeftAnnual()
            showModal() {//    
                this.$modal.show(Modal,{
                    hot_table : 'data',
                    modal : this.$modal },{
                    name: 'dynamic-modal',
                    width : '300px',
                    height : '225px',
                    draggable: false,
                })
            }// End - showModal()
            /* eslint-enable no-console */
        },// End - methods
        mounted() {
            // mounted     
            if (sessionStorage.length > 0) { //  sessionStorage    (  sessionStorage   )
                this.empId = sessionStorage.getItem("login_id");
                this.readTotalAnnual();
                this.readAnnuals();
            } else {
                this.$router.push("/");
            }
        },
        updated(){
            this.setLeftAnnual();
        }
    };
</script>

<style>
    .list {
        text-align: left;
        max-width: 450px;
        margin: auto;
    }
</style>
