<template>
    <div class="myInfo">
        <div class="img_div">
            <img v-bind:src="emp_img_url"><br>
        </div>
        <div class="infoDiv">
            <div class="empName"><strong>{{employee.emp_name}} {{employee.position}}</strong></div>
            <div class="depName">{{dep_name}}</div>
        </div>
    </div>
</template>

<script>
    import http from "../../http-common";

export default {
    name: "employee-info",
    data() {
        return {
          employee: {
            emp_id: "",
            emp_name: "",
            position: "",
            dep_id: ""
          },
          dep_name : "", //   
          emp_img_url : "" //   
        };
    },
    methods: {
        getEmpInfo(id) {    //  id this.employee.emp_id . : mounted()  .
            http
            .post("/mypage/" + id)
            .then(response => {
                //   employee  .
                this.employee.emp_pw = response.data.emp_pw;
                this.employee.emp_name = response.data.emp_name;
                this.employee.emp_email = response.data.emp_email;
                this.employee.position = response.data.position;
                this.employee.hire_date = response.data.hire_date;
                this.employee.extension_number = response.data.extension_number;
                this.employee.phone_number = response.data.phone_number;
                this.employee.dep_id = response.data.dep_id;

                this.getDep_Name(this.employee.dep_id);     //      
                this.emp_img_url = 'http://localhost:8080/emp_img/'+ this.employee.emp_id + '.jpg';  //    
            })
            .catch(e => {
                /* eslint-disable no-console */
                console.log(e);
            });
        }, // End - getEmpInfo :   , mounted()   .
        getDep_Name(dep_id) {
            http
            .post("/dep/" + dep_id)
            .then(response => {
                this.dep_name = response.data;
            })
            .catch(e => {
                /* eslint-disable no-console */
                console.log(e);
            });
        }, //   

    },
    mounted() {
        // mounted     
        if (sessionStorage.length > 0) { //  sessionStorage    (  sessionStorage   )
            this.employee.emp_id = sessionStorage.getItem("login_id");
            this.getEmpInfo(this.employee.emp_id); //   
        }else {
            this.$router.push("/");
        }
    } // End - mounted()

}
</script>

<style scoped>
    .myInfo {
        width: 15%;
        height: 40%;
        left: 15px;
        top: 70px;
        position: absolute;
        border: 1px solid gray;
    }
    img {
        width: 100%;
        object-fit: cover;
    }
    .img_div {
        width: 100px;
        height: 100px;
        border-radius: 70%;
        overflow: hidden;
        margin: 15% auto auto;
    }
    .infoDiv {
        margin-top: 20px;
    }
    .empName {
        font-size: 18px;
    }
    .depName {
        color: gray;
    }
</style>