<template>
    <div class="submitform">
        <table class="table table-hover">

            <tr>
                <th>ID</th>
                <td>{{outAddress.out_id}}</td>
            </tr>
            <tr>
                <th></th>
                <td><input type="text" placeholder=" " required v-model="outAddress.outName"></td>
            </tr>
            <tr>
                <th> </th>
                <td><input type="text" placeholder="  " required v-model="outAddress.out_mobile"></td>
            </tr>
            <tr>
                <th></th>
                <td><input type="text" placeholder=" " required v-model="outAddress.out_email"></td>
            </tr>
            <tr>
                <th></th>
                <td><input type="text" placeholder=" " required v-model="outAddress.outCompany"></td>
            </tr>
            <tr>
                <th>  </th>
                <td><input type="text" placeholder="  " required v-model="outAddress.out_dep_phone"></td>
            </tr>

        </table>
        <button class="btn btn-primary btn-lg" @click="updateOut(outAddress.out_id)"></button>
        <button class="btn btn-primary btn-lg" @click="deleteOut()"></button>
    </div>

</template>

<script>
    import http from "../../http-common";

    export default {
        name: "OutMgmDetail",
        props: ["outAddress"],
        data() {
            return {
                outAddress: {
                    out_id: "",
                    outName: "",
                    out_mobile: "",
                    out_email:"",
                    outCompany:"",
                    out_dep_phone:""
                },
                dep_name: "", //   
                emp_img_url: "" //   
            };
        },

        methods:{
            updateOut(id) {
                let data = {
                    outName: this.outAddress.outName,
                    out_mobile: this.outAddress.out_mobile,
                    out_email: this.outAddress.out_email,
                    outCompany: this.outAddress.outCompany,
                    out_dep_phone: this.outAddress.out_dep_phone

                }

                if (data.outName==""|data.out_mobile==""|data.out_email==""|
                    data.outCompany==""|data.out_dep_phone==""){ //    
                    alert("  !");
                } else {
                    http
                        .put("/outaddress/update/" + id, data) // UpfficeBack MyPageController  .
                        .then(response => {
                            if (response.data == 1) { //  1 1   ,   !
                                alert(" !");

                                this.$router.push("/manager/out-address")
                            }
                        })
                        .catch(e => {
                            console.log(e);
                        });
                }
            }, // End - updatePhone() :   ( , )
            deleteOut(){
                http
                    .delete("/outaddress/outaddress/"+ this.outaddress.out_id)
                    .then(response=>{
                        console.log(response.data);
                        this.$emit("refreshData");
                        this.$router.push('/manager/outaddress');
                    })
                    .catch(e=>{
                        console.log(e);
                    });

            }, //END- delete
        },
        mounted() {
            // mounted     
            if (sessionStorage.length > 0) { //  sessionStorage    (  sessionStorage   )
                /*this.id = this.employee.emp_id;*/ //manager_id...
                console.log(this.id);

            }else {
                this.$router.push("/manager");
            }
        } // End - mounted()
    }
</script>

<style scoped>
    .table {
        width: 100%;
        margin: auto;
        border: 1px solid #dddddd;
    }

    img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    th {
        vertical-align: middle;
        text-align: left;
    }
    td {
        text-align: left;
    }
</style>