<template>
    <div>
        <div>
            <table class="table table-hover mypageTable">
                <tr>
                    <td rowspan="6" width="25%">
                        <div class="img_div">
                            <img v-bind:src="emp_img_url"><br>
                        </div>
                    </td>
                    <th width="25%"></th>
                    <td width="50%">{{employee.emp_id}}</td>
                </tr>
                <tr>
                    <th></th>
                    <td><input class="form-control" type="password" placeholder="" required v-model="employee.emp_pw"></td>
                </tr>
                <tr>
                    <th> </th>
                    <td><input class="form-control" type="password" placeholder=" " required v-model="emp_pw_chk"></td>
                </tr>
                <tr>
                    <th></th>
                    <td>{{dep_name}}</td>
                </tr>
                <tr>
                    <th></th>
                    <td>{{employee.position}}</td>
                </tr>
                <tr>
                    <th></th>
                    <td>{{employee.extension_number}}</td>
                </tr>
                <tr>
                    <th rowspan="3" style="text-align: center"><span style="font-size: large">{{employee.emp_name}}</span></th>
                    <th></th>
                    <td>{{employee.hire_date}}</td>
                </tr>
                <tr>
                    <th></th>
                    <td>{{employee.emp_email}}</td>
                </tr>
                <tr>
                    <th> </th>
                    <td><input class="form-control" type="text" placeholder="000-0000-0000" required v-model="employee.phone_number"></td>
                </tr>
            </table><br>
        </div>
        <button class="btn btn-primary btn-lg" @click="updateInfo(employee.emp_id)"></button>
    </div>

</template>

<script>
    import http from "../../http-common";
export default {
    name: "employee-info",
    data() {
        return {
          employee: {
            emp_id: "",
            emp_pw: "",
            emp_name: "",
            emp_email: "",
            position: "",
            hire_date: "",
            extension_number: "",
            phone_number: "",
            dep_id: ""
          },
          dep_name : "", //   
          emp_pw_chk : "", //    
          emp_img_url : "" //   
        };
    },
    methods: {
        getEmpInfo(id) {    //  id this.employee.emp_id . : mounted()  .
            http
            .post("/mypage/" + id)
            .then(response => {
                //   employee  .
                this.employee.emp_pw = response.data.emp_pw;
                this.employee.emp_name = response.data.emp_name;
                this.employee.emp_email = response.data.emp_email;
                this.employee.position = response.data.position;
                this.employee.hire_date = response.data.hire_date;
                this.employee.extension_number = response.data.extension_number;
                this.employee.phone_number = response.data.phone_number;
                this.employee.dep_id = response.data.dep_id;

                this.getDep_Name(this.employee.dep_id);     //      
                this.emp_img_url = 'http://localhost:8080/emp_img/'+ this.employee.emp_id + '.jpg';  //    
            })
            .catch(e => {
                /* eslint-disable no-console */
                console.log(e);
            });
        }, // End - getEmpInfo :   , mounted()   .
        getDep_Name(dep_id) {
            http
            .post("/dep/" + dep_id)
            .then(response => {
                this.dep_name = response.data;
            })
            .catch(e => {
                /* eslint-disable no-console */
                console.log(e);
            });
        }, //   
        updateInfo(id) {
            let data = {
                emp_pw : this.employee.emp_pw,
                pw_chk : this.emp_pw_chk,
                phone_number : this.employee.phone_number
            }

            if (data.phone_number == "" | data.emp_pw == "" | data.pw_chk == ""){ //  , ,       
                alert("  !");
            } else if(data.emp_pw != data.pw_chk){  // ,      
                alert(" !");
            } else {
                http
                    .put("/mypage/update/phone/" + id, data) // UpfficeBack MyPageController  .
                    .then(response => {
                        if (response.data == 1) { //  1 1   ,   !
                            alert(" !");

                            //    
                            for(let i=0; i<1; i++) {
                                location.reload();
                            }
                        }
                    })
                    .catch(e => {
                        console.log(e);
                    });
            }
        } // End - updatePhone() :   ( , )

    },
    mounted() {
        // mounted     
        if (sessionStorage.length > 0) { //  sessionStorage    (  sessionStorage   )
            this.employee.emp_id = sessionStorage.getItem("login_id");
            this.getEmpInfo(this.employee.emp_id); //   
        }else {
            this.$router.push("/");
        }
    } // End - mounted()

}
</script>

<style scoped>
    .mypageTable {
        width: 80%;
        margin: 20px auto auto;
        border: 1px solid #dddddd;
    }
    .form-control {
        width: 200px;
    }
    img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
    .img_div {
        margin: 50px auto auto;
        width: 250px;
        height: 250px;
        border-radius: 70%;
        overflow: hidden;
    }
    th {
        vertical-align: middle;
        text-align: left;
    }
    td {
        text-align: left;
    }
</style>